@media (max-width: 1024px) {
  :root {
    --big-font: 4rem;
    --h2-font: 2.5rem;
    --p-font: 1rem;
    transition: 0.1s;
  }

  #menu-icon {
    display: block;
    font-size: 20px;
  }

  .navlist {
    position: absolute;
    top: -1000px;
    right: 0;
    left: 0;
    display: flex;
    flex-direction: column;
    background: var(--bg-color);
    text-align: left;
    transition: all 0.45s ease;
  }

  .navlist a {
    display: block;
    margin: 17px;
    font-size: 20px;
    transition: all 0.45s ease;
    color: var(--text-color);
  }

  .navlist a.active {
    color: var(--text-color);
  }

  .navlist a:hover {
    color: var(--main-color);
  }

  .navlist.open {
    top: 100%;
  }

  .home-text h3 {
    font-size: 2.5rem;
  }

  #home {
    height: 87vh;
  }

  #stat {
    /* margin-top: 50px; */
    display: grid;
    justify-content: center;
    /* border: 1px solid rebeccapurple; */
  }

  #calendar {
    width: 100%;
  }

  .ht1 {
    width: 100%;
  }

  #nav-link-projects {
    display: flex;
    /* grid-template-columns: repeat(auto-fit, minmax(350px, auto)); */
    /* grid-template-columns: 1fr; */
    flex-direction: column;
  }

  .project-card {
    display: flex;
    flex-direction: column;
    border: 3px solid #42b2e6;
    padding: 5px;
  }

  .project-card img {
    width: 100%;
  }

  .layer {
    width: 100%;
    border-radius: 8px;
    height: 100%;
    position: absolute;
    overflow: hidden;
    left: 0;
    bottom: 0;
    text-align: center;
    display: flex;
    flex-direction: column;
    background: linear-gradient(rgba(0, 0, 0, 0.1), #42b2e6);
    justify-content: center;
    align-items: center;
    transition: height 0.5s;
    padding: 0 40px;
  }

  #github-top-langs {
    /* border: 1px solid crimson; */
    height: 195px;
    margin-top: 0px;
  }
}

