/* dropDown menu */

.dropdown-toggle {
  background-color: #42b2e6;
  display: inline-block;
  cursor: pointer;
  padding: 10px 15px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.dropdown-menu {
  position: absolute;
  overflow-y: auto;
  top: 70px;
  left: 0;
  width: 99.5%;
  background-color: #070707;
  border-radius: 5px;
  border: 1px solid #ccc;
  max-height: 500px;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.2);
}

.dropdown-item {
  cursor: pointer;
  padding: 10px 15px;
  color: #42b2e6;
  width: 100%;
  border-bottom: 1px solid #42b2e6;
}